  @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html {
    box-sizing: border-box;
    scroll-behavior: smooth;
  }

body {
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    
  }

  .wrapper {
      width: 1440px;
     /* min-height: 800px;*/
      display: flex;
      margin: 0 auto;
      background-color: #FAFAFA;
      border: 0.5px solid rgba(227, 227, 227, 0.5);
      
        
  }

  .login_wrapper,
  .registration_wrapper {
      margin: 90px auto;
      width: 510px;
      height: 540px;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: white;
      border-radius: 5px;
      border: 1px solid rgba(39, 39, 39, 0.10);
  }
  .registration_wrapper {
    margin: 30px auto;
    height: 730px;
  }

  .logo {
      width: 88px;
      height: 88px;
      margin-top: 40px;
      margin-bottom: 32px;
   }

  .form_title {
      color: #000;
      text-align: center;
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 20px;
  }

  .login_form,
  .form_register {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
  }
  .login_block,
  .email_block,
  .password_block,
  .username_block,
  .age_block {
    margin-bottom: 15px;
   
  }

  .field_name {
      color: rgb(164, 164, 164);
      line-height: 22px;
      margin-bottom: 0;
      font-size: 16px;
      }

      .gender_block {
        align-self: flex-start;
        margin-left: 10px;
       }

  .field_gender {
    margin-right: 15px;
  }

  .log_input,
  .password_input {
      width: 334px;
      height: 48px;
      border-radius: 5px;
      font-size: 20px;
      padding: 12px 15px;
     border: 1px solid lightgrey;
    }

  .log_input:focus,
  .log_input:active,
  .password_input:focus     {
    background-color: rgba(197, 228, 249, 0.30);
    border: 1px solid none;
    outline: 1px solid rgb(98, 171, 196);
  }

  .log_input:hover {
    border: 1px solid none;
    outline: 1px solid rgb(98, 171, 196);
  }

  .button_login,
  .button_register {
      margin-top: 20px;
      margin-bottom: -10px;
      width: 176px;
      height: 52px;
      border-radius: 5px;
      border: 1px solid #1390E5;
      background-color: #1390E5;
      text-align: center;
      font-size: 20px;
      font-weight: 400;
      line-height: 24px;
      color: white;
  }

  .redirect_block {
    color: rgb(164, 164, 164);
    margin-left: -85px;
   }

  .redirect_link {
    color:#2c2ea7;

  }

  .redirect_link:hover {
    color:rgb(171, 74, 171);
  }

  .error_block {
    display: flex;
   width: 250px;
   height: 35px;
   margin-top: 10px;
    margin-left: -70px;
    color:#2c2ea7;
     
    }

    .password_info {
      margin-top: 25px;
      color: gray;
      font-size: 15px;
      line-height: 0px;
    }
    .password_info.second {
      margin-bottom: 10px;
    }

  /*------------search------------*/

  .header_wrapper {
    width: 1438px;
    height: 80px;
    background-color: white;
    border-bottom: 0.5px solid rgba(227, 227, 227, 0.5); ;
  
  
  }

  .nav {
    width: 1040px;
    height: 80px;
    margin-left: 200px;
    display: flex;


  }

  .header_logo {
    width: 48px;
    height: 48px;
    margin-top: 16px;
  }

.nav_item {
       color: #1390E5;
      font-size: 18px;
      font-style: normal;
      line-height: 28px;
      margin-left: 20px;
      padding: 26px 15px 0px 20px;
      text-decoration: none;
    }

.nav_item:hover {
      color:rgb(171, 74, 171);
