.nav_item:last-child {
      margin-left: 639px;
   
    }
    
 .search_body,
 .favorites_body {
  width: 1440px;
  min-height: 720px;
  }
    
.search_container,
.favorites_container {
  width: 1040px;
  min-height: 720px;
  margin-left: 200px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
    }

.search_title {
font-size: 36px;
font-weight: 400;
margin-top: 220px;
margin-bottom: 40px;
text-align: center;

}

.input_container {
  display: flex;
  height: 52px;
  width: 688px;
  text-align: center;
  border-radius: 5px;
  position: relative;

}


.search_input {
width: 536px;
font-size: 20px;
padding: 14px 15px;
background-color: white;
border-radius: 5px 0px 0px 5px;
box-sizing: border-box;
border: 1px solid rgba(23, 23, 25, 0.20);

}
.favorites_icon {
 position: absolute;
 right: 160px;
 top: 15px;
}


.search_input:focus {
  height: 50px;
  margin-top: 1px;
  background-color: rgba(197, 228, 249, 0.30);
  border: none;
  outline: 1px solid rgb(127, 182, 201);
  }

  .search_input::placeholder {
  color: rgb(164, 164, 164);
  }



.button_find {
  width: 150px;
  height: 52px;
  border-radius: 0px 5px 5px 0px;
  border: none;
  background-color: #1390E5;
  text-align: center;
  font-size: 20px;
  line-height: 24px;
  color: white;
  box-sizing: border-box;
}

/*------------result-------*/

.result_container {
  width: 1040px;
  min-height: 720px;
  margin-left: 200px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: left;

    }

 .search_title.result {
      margin-top: 40px;
      margin-bottom: 12px;
      font-size: 28px;
      line-height: 40px;
      text-align: left;
      
      }

.result_container .input_container {
  width: 1040px; 
}

.result_container .search_input {
  width: 890px;
}

.result_container .search_input::placeholder {
  visibility: hidden;
}

.filter_panel_container {
  width: 1040px;
  height: 24px;
  margin-top: 40px;
  margin-bottom: 20px;
   display: flex;
  justify-content: space-between;
}

.filter_title_wrapper {
  display: flex;
}


.filter_title {
  width: 310 px;
  font-weight: 500;
  line-height: 24px;
  margin-right: 15px;
 
}

.filter_number_cards {
font-style: normal;
font-weight: 400;
color: rgba(23, 23, 25, 0.30);
}

.list_image,
.grid_image {
  width: 24px;
  height: 24px;
  margin-left: 15px;
  margin-right: 0;
  color:lightgrey;
}

.videos_container {
  width: 867px;
  min-height: 818px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
 
}

.videos_container.list_mode {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
}

.videos_container.grid_mode {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 28px 20px;

}/*----list_mode----*/
.list_mode .video_item {
  width: 867px;
  height: 88px;
  display: flex;
  text-decoration: none;
}

.list_mode .video_image {
  width: 157px;
  height: 88px;
  margin-right: 20px;
}

.list_mode .video_item_details {
  display: flex;
  flex-direction: column;
  width: 510px;
  height: 88px;
}

.list_mode .item_title {
  width: 510px;
  height: 40px;
  margin-bottom: 4px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  color:black;
  overflow: hidden;
}

.list_mode .item_description,
.list_mode .view_count {
  width: 510px;
  height: 20px;
  overflow: hidden;
  margin-bottom: 2px;
  font-size: 16px;
  color: rgba(23, 23, 25, 0.30);
}

/*----grid_mode----*/
.grid_mode .video_item {
  width: 245px;
  height: 226px;
  display: flex;
  flex-direction: column;
  text-decoration: none;
}

.grid_mode .video_image {
  width: 245px;
  height: 138px;
  margin-bottom: 8px;
}

.grid_mode .video_item_details {
  display: flex;
  flex-direction: column;
  width: 245px;
  height: 80px;
}

.grid_mode .item_title {
  width: 245px;
  height: 32px;
  margin-bottom: 8px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  color:black;
  overflow: hidden;
}

.grid_mode .item_description,
.grid_mode .view_count {
  width: 245px;
  height: 16px;
  overflow: hidden;
  margin-bottom: 2px;
  font-size: 14px;
  color: rgba(23, 23, 25, 0.30);
}
